export class ExpensePolicyViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#expense_policy_view_container',

			type_array: null,
			require_receipt_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'ExpensePolicyEditView.html';
		this.permission_id = 'expense_policy';
		this.viewId = 'ExpensePolicy';
		this.script_name = 'ExpensePolicyView';
		this.table_name_key = 'expense_policy';
		this.context_menu_name = $.i18n._( 'Expense Policy' );
		this.navigation_label = $.i18n._( 'Expense Policy' ) + ':';
		this.api = TTAPI.APIExpensePolicy;
		this.render();
		this.buildContextMenu();

		this.initData();
		this.setSelectRibbonMenuIfNecessary( 'ExpensePolicy' );
	}

	initOptions() {
		var $this = this;
		this.initDropDownOption( 'type' );
		this.initDropDownOption( 'require_receipt' );
	}

	buildEditViewUI() {

		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_expense_policy': { 'label': $.i18n._( 'Expense Policy' ) },
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		this.navigation.AComboBox( {
			api_class: TTAPI.APIExpensePolicy,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_expense_policy',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_expense_policy = this.edit_view_tab.find( '#tab_expense_policy' );

		var tab_expense_policy_column1 = tab_expense_policy.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_expense_policy_column1 );

		//Name
		var form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Name' ), form_item_input, tab_expense_policy_column1, 'first' );

		form_item_input.parent().width( '45%' );

		// Description
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_AREA );
		form_item_input.TTextArea( { field: 'description', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Description' ), form_item_input, tab_expense_policy_column1, '', null, null, true );

		form_item_input.parent().width( '45%' );

		// Type
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );

		form_item_input.TComboBox( { field: 'type_id', set_empty: false } );
		form_item_input.setSourceData( $this.type_array );
		this.addEditFieldToColumn( $.i18n._( 'Type' ), form_item_input, tab_expense_policy_column1 );

		// Unit Name
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'unit_name', width: 200 } );
		this.addEditFieldToColumn( $.i18n._( 'Unit Name' ), form_item_input, tab_expense_policy_column1, '', null, true );

		// Percent

		// Amount/Unit

		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'amount', width: 100 } );
		this.addEditFieldToColumn( $.i18n._( 'Percent' ), form_item_input, tab_expense_policy_column1, '', null, true );

		// Minimum Amount
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'min_amount', width: 100 } );
		this.addEditFieldToColumn( $.i18n._( 'Minimum Amount' ), form_item_input, tab_expense_policy_column1 );

		// Maximum Amount
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'max_amount', width: 100 } );
		this.addEditFieldToColumn( $.i18n._( 'Maximum Amount' ), form_item_input, tab_expense_policy_column1 );

		// Taxes
		var default_args = {};
		default_args.filter_data = {};
		default_args.filter_data.type_id = [40, 50];

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIExpensePolicy,
			allow_multiple_selection: true,
			layout_name: 'global_expense_policy',
			set_empty: true,
			show_search_inputs: true,
			field: 'expense_policy'
		} );

		form_item_input.setDefaultArgs( default_args );

		this.addEditFieldToColumn( $.i18n._( 'Taxes' ), form_item_input, tab_expense_policy_column1 );

		// Require Receipt
		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );

		form_item_input.TComboBox( { field: 'require_receipt_id', set_empty: false } );
		form_item_input.setSourceData( $this.require_receipt_array );
		this.addEditFieldToColumn( $.i18n._( 'Require Receipt' ), form_item_input, tab_expense_policy_column1 );

		// Reimbursable
		form_item_input = Global.loadWidgetByName( FormItemType.CHECKBOX );
		form_item_input.TCheckbox( { field: 'is_reimbursable' } );
		this.addEditFieldToColumn( $.i18n._( 'Reimbursable' ), form_item_input, tab_expense_policy_column1 );

		// Pay Stub Account

		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIPayStubEntryAccount,
			allow_multiple_selection: false,
			layout_name: 'global_PayStubAccount',
			set_empty: true,
			show_search_inputs: true,
			field: 'pay_stub_entry_account_id'
		} );

		this.addEditFieldToColumn( $.i18n._( 'Pay Stub Account' ), form_item_input, tab_expense_policy_column1, '', null, true );

		// Product
		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIProduct,
			allow_multiple_selection: false,
			layout_name: 'global_product',
			set_empty: true,
			show_search_inputs: true,
			field: 'product_id'
		} );

		this.addEditFieldToColumn( $.i18n._( 'Product' ), form_item_input, tab_expense_policy_column1 );
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Name' ),
				in_column: 1,
				field: 'name',
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Type' ),
				in_column: 1,
				field: 'type_id',
				multiple: true,
				basic_search: true,
				adv_search: false,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Require Receipt' ),
				in_column: 1,
				field: 'require_receipt_id',
				multiple: true,
				basic_search: true,
				adv_search: false,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Product' ),
				in_column: 2,
				field: 'product_id',
				layout_name: 'global_product',
				api_class: TTAPI.APIProduct,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: true,
				adv_search: false,
				form_item_type: FormItemType.AWESOME_BOX
			} )
		];
	}

	onFormItemChange( target, doNotValidate ) {
		this.setIsChanged( target );
		this.setMassEditingFieldsWhenFormChange( target );

		var key = target.getField();

		this.current_edit_record[key] = target.getValue();

		if ( key === 'type_id' || key === 'is_reimbursable' ) {
			this.onTypeChange();
		}

		if ( !doNotValidate ) {
			this.validate();
		}
	}

	setCurrentEditRecordData() {

		// When mass editing, these fields may not be the common data, so their value will be undefined, so this will cause their change event cannot work properly.
		this.setDefaultData( {
			'type_id': 10
		} );

		//Set current edit record data to all widgets
		for ( var key in this.current_edit_record ) {

			if ( !this.current_edit_record.hasOwnProperty( key ) ) {
				continue;
			}

			var widget = this.edit_view_ui_dic[key];
			if ( Global.isSet( widget ) ) {
				switch ( key ) {
					case 'pay_stub_entry_account_id': //popular case
						widget.setValue( this.current_edit_record[key] );
						break;
					default:
						widget.setValue( this.current_edit_record[key] );
						break;
				}

			}
		}

		this.onTypeChange();
		this.collectUIDataToCurrentEditRecord();
		this.setEditViewDataDone();
	}

	onTypeChange() {
		if ( this.current_edit_record['type_id'] == 10 ) {
			this.detachElement( 'amount' );
			this.detachElement( 'unit_name' );
		} else if ( this.current_edit_record['type_id'] == 20 || this.current_edit_record['type_id'] == 40 ) {
			this.edit_view_form_item_dic['amount'].find( '.edit-view-form-item-label' ).text( $.i18n._( 'Percent' ) + ': ' );
			this.attachElement( 'amount' );
			this.detachElement( 'unit_name' );
		} else if ( this.current_edit_record['type_id'] == 30 ) {
			this.edit_view_form_item_dic['amount'].find( '.edit-view-form-item-label' ).text( $.i18n._( 'Amount/Unit' ) + ': ' );
			this.attachElement( 'amount' );
			this.attachElement( 'unit_name' );
		} else if ( this.current_edit_record['type_id'] == 50 ) {
			this.edit_view_form_item_dic['amount'].find( '.edit-view-form-item-label' ).text( $.i18n._( 'Amount' ) + ': ' );
			this.detachElement( 'unit_name' );
		} else {
			this.detachElement( 'amount' );
			this.detachElement( 'unit_name' );
		}

		if ( this.edit_view_ui_dic.is_reimbursable.getValue() == true ) {
			this.attachElement( 'pay_stub_entry_account_id' );
		} else {
			this.detachElement( 'pay_stub_entry_account_id' );
		}

		this.editFieldResize();

	}

}